<template>
  <div class="app-container">
    <div class="table_select_tag">
      <el-tag v-for="item in multipleSelection" :key="item.date" effect="dark" closable @close="tableSelectTagClose(item)">
        {{ item.name }}
      </el-tag>
    </div>
    <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%;" @selection-change="handleSelectionChange" align="center">
      <el-table-column type="selection" width="55" align="center"> </el-table-column>
      <el-table-column label="日期" width="120" align="center">
        <template slot-scope="scope" align="center">{{ scope.row.date }}</template>
      </el-table-column>
      <el-table-column prop="name" label="姓名" width="120" align="center"> </el-table-column>
      <el-table-column prop="address" label="地址" show-overflow-tooltip align="center"> </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "",
  components: {},
  mixins: [],
  props: {},
  data() {
    return {
      tableData: [
        {
          date: "2016-05-03",
          name: "王1",
          address: "上海市普陀区金沙江路 1518 弄",
          id:1
        },
        {
          date: "2016-05-02",
          name: "王2",
          address: "上海市普陀区金沙江路 1518 弄",
          id:2
        },
        {
          date: "2016-05-04",
          name: "王3",
          address: "上海市普陀区金沙江路 1518 弄",
          id:3
        },
        {
          date: "2016-05-01",
          name: "王4",
          address: "上海市普陀区金沙江路 1518 弄",
          id:4
        },
        {
          date: "2016-05-08",
          name: "王5",
          address: "上海市普陀区金沙江路 1518 弄",
          id:5
        },
        {
          date: "2016-05-06",
          name: "王6",
          address: "上海市普陀区金沙江路 1518 弄",
          id:6
        },
        {
          date: "2016-05-07",
          name: "王7",
          address: "上海市普陀区金沙江路 1518 弄",
          id:7
        },
      ],
      multipleSelection: [],
    };
  },
  computed: {},
  watch: {},
  mounted() {
    var _this = this;
    var rows = _this.multipleSelection;
    var mapRows = rows.map((item) => item.id);
    console.log(mapRows);
    this.$nextTick(function () {
      this.tableData.forEach((element) => {
        if (mapRows.includes(element.id)) {
          this.$refs.multipleTable.toggleRowSelection(element, true);
        }
      });
    });
  },
  methods: {
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    tableSelectTagClose(data){
      var dataArray = [data]
      var mapRows = dataArray.map((item) => item.id);
      this.$nextTick(function () {
        this.tableData.forEach((element) => {
          if (mapRows.includes(element.id)) {
            this.$refs.multipleTable.toggleRowSelection(element, false);
          }
        });
      });
    }
  },
};
</script>
<style lang="scss" scoped>
.table_select_tag{
  min-height: 80px;
  border: 1px solid #8c8c8c;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
  .el-tag{
    margin-right: 10px;
  }
}
</style>
