export const commonMixin = {
  data() {
    return {
      downloadLoadingByWeb: false
    }
  },
  methods: {
    unique(arr) {
      var newArr = [arr[0]];
      for (var i = 1; i < arr.length; i++) {
        if (newArr.indexOf(arr[i]) === -1) {
          newArr.push(arr[i]);
        }
      }
      return newArr;
    },
    onContextMenu(bool){
      return bool
    },
    //前端导出
    exportListByWeb(obj) {
      this.downloadLoadingByWeb = true;
      import("@/utils/Export2Excel").then((excel) => {
        const tHeader = obj.tHeader || [];
        const filterVal = obj.filterVal || [];
        const list = obj.dataList || [];
        const data = this.formatJsonByWeb(filterVal, list);
        excel.export_json_to_excel({
          header: tHeader,
          data,
          filename: obj.filename || '数据',
          autoWidth: true,
          bookType: "xlsx",
        });
        this.downloadLoadingByWeb = false;
      });
    },
    formatJsonByWeb(filterVal, jsonData) {
      return jsonData.map((v) =>
        filterVal.map((j) => {
          return v[j];
        })
      );
    },
  },
}