<template>
  <div class="app-container">
    <el-table :data="tableData" ref="dragTable" border style="width: 100%;" @header-contextmenu="onHeaderClick" @header-click="showList = false" row-key="id">
      <!-- <el-table-column prop="date" label="日期" align="center"> </el-table-column>
      <el-table-column prop="name" label="姓名" align="center"> </el-table-column>
      <el-table-column prop="address" label="地址" align="center"> </el-table-column>
      <el-table-column prop="age" label="年龄" align="center"> </el-table-column>
      <el-table-column prop="sex" label="性别" align="center"> </el-table-column> -->
      <el-table-column
        :min-width="100"
        v-for="(item, index) in tabColumnList"
        :prop="item.prop"
      :label="tabColumnList2[index].label"
        align="center"
        :column-key="rowKey(item, index)"
        :key="rowKey(item, index)"
        v-show="item.checked"
      >
      </el-table-column>
    </el-table>
    <el-card class="box-card table_header_prop_list" v-show="showList" :style="{ left: table_header_prop_left, top: table_header_prop_top }">
      <div class="text item" @contextmenu.prevent="onContextMenu">
        <el-checkbox-group @change="handleCheckedCitiesChange" v-model="checkedCities" class="table_header_prop_checkbox">
          <el-checkbox v-for="(item, index) in tabColumnList3" :key="index" :label="item.prop"> {{ item.label }}</el-checkbox>
        </el-checkbox-group>
      </div>
    </el-card>
  </div>
</template>

<script>
import Sortable from "sortablejs";
export default {
  data() {
    return {
      showList: false,
      table_header_prop_left: 0,
      table_header_prop_top: 0,
      checkedCities: ["date", "name", "sex", "address", "age"],
      tabColumnList: [
        {
          prop: "date",
          label: "日期",
          checked: true,
        },
        {
          prop: "name",
          label: "姓名",
          checked: true,
        },
        {
          prop: "sex",
          label: "性别",
          checked: true,
        },
        {
          prop: "address",
          label: "地址",
          checked: true,
        },
        {
          prop: "age",
          label: "年龄",
          checked: true,
        },
      ],

      keyObj: {
        date: "日期",
        name: "姓名",
        sex: "性别",
        address: "地址",
        age: "年龄",
      },

      tabColumnList2: [
        {
          prop: "date",
          label: "日期",
          checked: true,
        },
        {
          prop: "name",
          label: "姓名",
          checked: true,
        },
        {
          prop: "sex",
          label: "性别",
          checked: true,
        },
        {
          prop: "address",
          label: "地址",
          checked: true,
        },
        {
          prop: "age",
          label: "年龄",
          checked: true,
        },
      ],
      tabColumnList3: [
        {
          prop: "date",
          label: "日期",
          checked: true,
        },
        {
          prop: "name",
          label: "姓名",
          checked: true,
        },
        {
          prop: "sex",
          label: "性别",
          checked: true,
        },
        {
          prop: "address",
          label: "地址",
          checked: true,
        },
        {
          prop: "age",
          label: "年龄",
          checked: true,
        },
      ],

      tableData: [
        {
          date: "2016-05-02",
          name: "王小虎2",
          address: "上海市普陀区金沙江路 1518 弄",
          age: "18",
          sex: "男",
          id: 1,
        },
        {
          date: "2016-05-04",
          name: "王小虎4",
          address: "上海市普陀区金沙江路 1517 弄",
          age: "18",
          sex: "男",
          id: 2,
        },
        {
          date: "2016-05-01",
          name: "王小虎1",
          address: "上海市普陀区金沙江路 1519 弄",
          age: "18",
          sex: "男",
          id: 3,
        },
        {
          date: "2016-05-03",
          name: "王小虎3",
          address: "上海市普陀区金沙江路 1516 弄",
          age: "18",
          sex: "男",
          id: 4,
        },
      ],
    };
  },
  mounted() {
    // this.rowDrop()
    // this.columnDrop()
    this.setSort();
  },
  computed: {
    isCollapse(){
      return this.$store.state.sidebarIsCollapse
    },
  },
  methods: {
    rowKey(item, index) {
      let key = `col_${item.prop}_${index}_${Math.random()}`;
      return key;
    },
   
    onHeaderClick(column, event) {
      event.preventDefault();
      this.table_header_prop_left =this.isCollapse?event.x - 54 + "px":event.x - 170 + "px";
      this.table_header_prop_top = event.y - 88 + "px";
      this.showList = true;
    },
    onContextMenu() {
      return false;
    },
    handleCheckedCitiesChange(val) {
    },
    setSort() {
      const el = this.$refs.dragTable.$el.querySelector(".el-table__header-wrapper thead tr");
      this.sortable = Sortable.create(el, {
        ghostClass: "sortable-ghost",
        setData: function (dataTransfer) {
          dataTransfer.setData("Text", "");
        },
        onEnd: (evt) => {
          const tabColumnListRow = this.tabColumnList.splice(evt.oldIndex, 1)[0];
          this.tabColumnList.splice(evt.newIndex, 0, tabColumnListRow);
          const newArray = this.tabColumnList.slice(0);
          newArray.forEach((value, index) => {
            this.$set(this.tabColumnList, index, value);
            this.$set(this.tabColumnList2, index, value);
          });
        },
      });
    },
  },
  watch: {
    checkedCities: function (newVal, oldVal) {
      var that = this;
      let set_newVal = new Set(newVal);
      let set_oldVal = new Set(oldVal);
      let differenceABSet =
        newVal.length > oldVal.length
          ? Array.from(new Set([...set_newVal].filter((x) => !set_oldVal.has(x))))
          : Array.from(new Set([...set_oldVal].filter((x) => !set_newVal.has(x))));
      var isShowNewColumn = newVal.length > oldVal.length;
      this.tabColumnList.map((item, index) => {
        if (item.prop == differenceABSet[0]) {
          item.checked = !item.checked;
            that.tabColumnList.splice(index, 1);
        } else {
        }
      });
      this.tabColumnList2.map((item2, index2) => {
        if (item2.prop == differenceABSet[0]) {
          item2.checked = !item2.checked;
            that.tabColumnList2.splice(index2, 1);
        } else {
        }
      });
      if (isShowNewColumn) {
        this.tabColumnList3.map((sItem, sIndex) => {
          if (sItem.prop == differenceABSet[0]) {
            sItem.checked = true;
            
            that.tabColumnList.push(sItem)
            that.tabColumnList2.push(sItem)
          }
        });
      }
    },
  },
};
</script>
