import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import ElementUI from 'element-ui';
import '@/utils/global'//全局
import './promission'//这里进行路由后台获取的模拟
import 'element-ui/lib/theme-chalk/index.css';
import '@/styles/index.scss' // global css

import NProgress from 'nprogress'
import 'nprogress/nprogress.css'


import {commonMixin} from "./utils/mixin"
import axios from 'axios';
Vue.config.productionTip = false
Vue.use(ElementUI)
Vue.mixin(commonMixin)
Vue.prototype.$axios = axios


router.afterEach((to, from, next) => {
  var tagBarList = store.state.tagBarList || [];
  var tagBarPathList = store.state.tagBarPathList || [];
  if(!tagBarPathList.includes(to.path)){
    tagBarList.push({path:to.path,meta:to.meta})
    tagBarPathList.push(to.path)
  }
  store.dispatch("tagBarListAction",tagBarList).then(()=>{
    store.dispatch("tagBarPathListAction",tagBarPathList)
  })
  NProgress.done()
})



new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
