export const LGI =(key,jsonparse = true)=> {
  return jsonparse?JSON.parse(localStorage.getItem(key)):localStorage.getItem(key)
}
export const LSI =(key,data)=> {
  return localStorage.setItem(key,JSON.stringify(data))
}
export const SGI =(key,jsonparse = true)=> {
  return jsonparse?JSON.parse(sessionStorage.getItem(key)):sessionStorage.getItem(key)
}
export const SSI =(key,data)=> {
  return sessionStorage.setItem(key,JSON.stringify(data))
}
