import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
import {LGI,LSI,SGI,SSI} from "../utils/browserStorage"

export default new Vuex.Store({
  state: {
    sidebarIsCollapse:LGI("sidebarIsCollapse")==true?true:false,
    tagBarPathList:LGI("tagBarPathList")?LGI("tagBarPathList"):[],
    tagBarList:LGI("tagBarList")?LGI("tagBarList"):[]
  },
  mutations: {
    SET_SIDEBAR_IS_COLLAPSE(state){
      state.sidebarIsCollapse = !state.sidebarIsCollapse;
      LSI("sidebarIsCollapse",state.sidebarIsCollapse)
    },
    SET_TAB_BAR_LIST(state,list){
      state.tagBarList = list;
      LSI("tagBarList",list)
    },
    SET_TAB_BAR_PATH_LIST(state,list){
      state.tagBarPathList = list;
      LSI("tagBarPathList",list)
    }
  },
  actions: {
    sidebarIsCollapseAction({commit}){
      commit("SET_SIDEBAR_IS_COLLAPSE")
    },
    tagBarListAction({commit},list){
      commit("SET_TAB_BAR_LIST",list)
    },
    tagBarPathListAction({commit},list){
      commit("SET_TAB_BAR_PATH_LIST",list)
    },
  },
  modules: {
  }
})
