<template>
  <div id="navbar">
    <div class="navbar_left">
      <div class="sidebar_visibility_icon" @click="sidebarVisibility">
        <span class="sidebar_close" v-if="!isCollapse">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            aria-hidden="true"
            role="img"
            width="1em"
            height="1em"
            viewBox="0 0 1024 1024"
            class="iconify iconify--ant-design"
          >
            <path
              fill="currentColor"
              d="M408 442h480c4.4 0 8-3.6 8-8v-56c0-4.4-3.6-8-8-8H408c-4.4 0-8 3.6-8 8v56c0 4.4 3.6 8 8 8zm-8 204c0 4.4 3.6 8 8 8h480c4.4 0 8-3.6 8-8v-56c0-4.4-3.6-8-8-8H408c-4.4 0-8 3.6-8 8v56zm504-486H120c-4.4 0-8 3.6-8 8v56c0 4.4 3.6 8 8 8h784c4.4 0 8-3.6 8-8v-56c0-4.4-3.6-8-8-8zm0 632H120c-4.4 0-8 3.6-8 8v56c0 4.4 3.6 8 8 8h784c4.4 0 8-3.6 8-8v-56c0-4.4-3.6-8-8-8zM115.4 518.9L271.7 642c5.8 4.6 14.4.5 14.4-6.9V388.9c0-7.4-8.5-11.5-14.4-6.9L115.4 505.1a8.74 8.74 0 0 0 0 13.8z"
            ></path>
          </svg>
        </span>
        <span class="sidebar_open" v-else>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            aria-hidden="true"
            role="img"
            width="1em"
            height="1em"
            viewBox="0 0 1024 1024"
            class="iconify iconify--ant-design"
          >
            <path
              fill="currentColor"
              d="M408 442h480c4.4 0 8-3.6 8-8v-56c0-4.4-3.6-8-8-8H408c-4.4 0-8 3.6-8 8v56c0 4.4 3.6 8 8 8zm-8 204c0 4.4 3.6 8 8 8h480c4.4 0 8-3.6 8-8v-56c0-4.4-3.6-8-8-8H408c-4.4 0-8 3.6-8 8v56zm504-486H120c-4.4 0-8 3.6-8 8v56c0 4.4 3.6 8 8 8h784c4.4 0 8-3.6 8-8v-56c0-4.4-3.6-8-8-8zm0 632H120c-4.4 0-8 3.6-8 8v56c0 4.4 3.6 8 8 8h784c4.4 0 8-3.6 8-8v-56c0-4.4-3.6-8-8-8zM142.4 642.1L298.7 519a8.84 8.84 0 0 0 0-13.9L142.4 381.9c-5.8-4.6-14.4-.5-14.4 6.9v246.3a8.9 8.9 0 0 0 14.4 7z"
            ></path>
          </svg>
        </span>
      </div>
    </div>
    <div class="navbar_right">
      <!-- <div id="open_live2d">开启机器人</div> -->
      <span class="full_screen" @click="fullScreen">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          aria-hidden="true"
          role="img"
          width="0.8em"
          height="1em"
          viewBox="0 0 304 384"
          data-icon="zmdi:fullscreen"
          class="iconify iconify--zmdi"
        >
          <path fill="currentColor" d="M43 235v64h64v42H0V235h43zM0 149V43h107v42H43v64H0zm256 150v-64h43v106H192v-42h64zM192 43h107v106h-43V85h-64V43z"></path>
        </svg>
      </span>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  mixins: [],
  props: {},
  data() {
    return {
      hitokoto: "",
    };
  },
  created() {},
  computed: {
    isCollapse() {
      return this.$store.state.sidebarIsCollapse;
    },
  },
  watch: {},
  mounted() {
    // this.getAwordOfTheDay()
  },
  methods: {
    sidebarVisibility() {
      this.$store.dispatch("sidebarIsCollapseAction");
    },
    getAwordOfTheDay() {
      this.$axios
        .get("https://v1.hitokoto.cn/")
        .then((res) => {
          if (res.status == 200) {
            this.hitokoto = res.data.hitokoto;
          }
        })
        .catch((err) => {
        });
    },
    fullScreen() {
      if (document.documentElement.RequestFullScreen) {
        document.documentElement.RequestFullScreen();
      }
      //兼容火狐
      if (document.documentElement.mozRequestFullScreen) {
        document.documentElement.mozRequestFullScreen();
      }
      //兼容谷歌等可以webkitRequestFullScreen也可以webkitRequestFullscreen
      if (document.documentElement.webkitRequestFullScreen) {
        document.documentElement.webkitRequestFullScreen();
      }
      //兼容IE,只能写msRequestFullscreen
      if (document.documentElement.msRequestFullscreen) {
        document.documentElement.msRequestFullscreen();
      }
    },
  },
};
</script>
<style lang="scss" scoped>
#navbar {
  height: 50px;
  box-sizing: border-box;
  border-bottom: 2px solid #f5f7f5;
  line-height: 50px;
  padding: 0 20px;
  background: #ffffff;
  display: flex;
  .navbar_left {
    flex: 1;
    .sidebar_visibility_icon {
      display: inline-block;
    }
  }
  .navbar_right {
    flex: 1;
    text-align: right;
    #open_live2d {
      cursor: pointer;
    }
  }
}
</style>
