<template>
  <div id="tabBarContainer">
    <draggable v-model="tagBarPathList" group="people" @start="drag = true" @end="drag = false">
      <el-tag
        v-for="(item, index) in tagBarList"
        :key="index"
        :effect="item.path == currentRouterPath ? 'dark' : 'plain'"
        :closable="true"
        class="tag_item"
        @click="tagBarClick(item)"
        @close="tagBarClose(item)"
        :data-path="item.path"
      >
        {{ item.meta.title }}
      </el-tag>
    </draggable>
  </div>
</template>
<script>
import draggable from "vuedraggable";
export default {
  name: "",
  components: { draggable },
  mixins: [],
  props: {
    showTagBarList: {
      type: Array,
      default: [],
    },
    showtagBarPathList: {
      type: Array,
      default: [],
    },
  },
  data() {
    return {
      tagBarList: [],
      tagBarPathList: [],
    };
  },
  computed: {
    currentRouterPath() {
      return this.$route.path;
    },
    isCollapse() {
      return this.$store.state.sidebarIsCollapse;
    },
  },
  watch: {},
  mounted() {
    this.tagBarList = this.showTagBarList;
    this.tagBarPathList = this.showtagBarPathList;
  },
  methods: {
    tagBarClick(tag) {
      this.$router.push({ path: tag.path });
    },
    tagBarClose(tag) {
      var tagBarPathList = this.$store.state.tagBarPathList;
      var tagBarList = this.$store.state.tagBarList;
      var tagIndexTotagBarPathList = tagBarPathList.findIndex((key) => tag.path == key);
      var tagIndexToTagBarList = tagBarList.findIndex((key) => tag.path == key.path);
      var showRoutePath = "";
      if (tagBarList.length > 1) {
        if (tagIndexToTagBarList == 0) {
          //删除第一个 往后面看
          if (tag.path == this.$route.path) showRoutePath = tagBarPathList[tagIndexToTagBarList + 1];
        } else {
          //删除非第一个,往前看
          if (tag.path == this.$route.path) showRoutePath = tagBarPathList[tagIndexToTagBarList - 1];
        }
      } else {
        if (tag.path == "/dashboard") this.$message.error("只有一个tag页签时不允许删除");
        showRoutePath = "/dashboard";
      }
      var newTagBarList = tagBarList.filter((item) => item.path != tag.path);
      var newtagBarPathList = tagBarPathList.filter((item) => item != tag.path);
      if (newtagBarPathList.length == 0) {
        newtagBarPathList = ["/dashboard"];
      }
      if (newTagBarList.length == 0) {
        newTagBarList = [
          {
            meta: { title: "首页", icon: "dashboard" },
            path: "/dashboard",
          },
        ];
      }
      this.$store.dispatch("tagBarListAction", newTagBarList).then(() => {
        this.$store.dispatch("tagBarPathListAction", newtagBarPathList).then(() => {
          this.$router.push({ path: showRoutePath });
        });
      });
    },
    // tagMouseDown(e){
    // var _this = this;
    // if(e.button == 2){
    //   var el= e.target
    //   var nodeName = el.nodeName.toLowerCase();
    //   var parentNodeName = el.parentNode.nodeName.toLowerCase();
    //   if(nodeName == 'span' || parentNodeName=='span'){
    //     var path = parentNodeName=='span'?el.parentNode.dataset.path:el.dataset.path
    //     var tagIndexTotagBarPathList = _this.tagBarPathList.findIndex((key) => path == key);
    //     // this.table_header_prop_left =this.isCollapse?event.x - 54 + "px":event.x - 170 + "px";
    //     // this.table_header_prop_top = event.y - 88 + "px";
    //     // this.showList = true;

    //   }
    // }
    // }
  },
  watch: {
    showTagBarList: function (newVal, oldVal) {
      this.tagBarList = newVal;
    },
    showtagBarPathList: function (newVal, oldVal) {
      this.tagBarPathList = newVal;
    },
    tagBarPathList: function (newVal, oldVal) {
      var _this = this;
      var tagBarList = this.tagBarList;
      if (oldVal.length > 0) {
        tagBarList.sort((star, next) => {
          const d = _this.unique([...newVal]);
          return d.indexOf(star.path) - d.indexOf(next.path);
        });
      }
      this.$store.dispatch("tagBarListAction", tagBarList).then(() => {
        this.$store.dispatch("tagBarPathListAction", newVal);
      });
    },
  },
};
</script>
<style lang="scss" scoped>
#tabBarContainer {
  height: 38px;
  line-height: 38px;
  padding: 0 20px;
  background: #ffffff;
  .tag_item {
    cursor: pointer;
    user-select: none;
    margin-right: 5px;
  }
}
</style>
